<template>
  <div v-loading='loading'>
    <cms-breadcrumb to="/admin" @click="rest">
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="cms-form-layout">
      <cms-prompt collapse='true'>
        <li>可进行添加管理员，用于登录平台管理，无法登录商城前台及商家管理平台</li>
        <li>管理员必须赋予角色权限，如无角色权限请移至系统->权限->角色管理中新增</li>
        <li>保存并新增按钮触发之后需再次跳转回该页面</li>
      </cms-prompt>
      <el-form ref="form" :rules="saveRules" :model="formData" label-width="210px">
        <el-form-item label="用户名" prop="username" :error='errorMessage.username'>
          <el-input v-model="formData.username" :autofocus="true" ref="first" ></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password"  :error='errorMessage.password' >
          <el-input v-model="formData.password" type="password" maxlength="20"></el-input>
        </el-form-item>
        <el-form-item label="确认密码" prop="checkPassword"  :error='errorMessage.checkPassword' >
          <el-input v-model="formData.checkPassword" type="password" maxlength="20"></el-input>
        </el-form-item>
        <el-form-item label="Email" prop="email" :error='errorMessage.email'>
          <el-input v-model="formData.email" maxlength="50"></el-input>
        </el-form-item>
        <el-form-item label="是否启用" prop="enabled" :error='errorMessage.enabled'>
          <cms-switch v-model="formData.enabled"></cms-switch>
        </el-form-item>
        <el-form-item label="角色" prop="roleid"  :error='errorMessage.roleid'>
          <el-checkbox-group v-model="formData.roleid">
            <el-checkbox v-for="item in roleList" :key="item.id" :label="item.id">
              {{item.roleName}}
            </el-checkbox>
          </el-checkbox-group>
        </el-form-item>
      </el-form>
      <div class="cms-form-footer">
        <cms-button  @click="save(false)" v-color >保存并新增</cms-button>
        <cms-button @click="save(true)"  v-color >保存</cms-button>
        <cms-button type='rest' @click="rest">重置</cms-button>
      </div>
    </div>
  </div>
</template>
<script>
import rules from '@/rules'
import form from '@/mixins/form.js'
import { coreRolePage } from '@/api/admin/role.js'
import { adminSave, validName, validMail } from '@/api/admin/admin.js'

var defaultData = {
  'username': '',
  'password': '',
  'checkPassword': '',
  'email': '',
  'enabled': true,
  'roleid': []
}
export default {
  name: 'adminSave',
  mixins: [form],
  data() {
    var validateName = (rule, value, callback) => {
      // 验证用户名的唯一性
      validName({
        validName: this.formData.username
      }).then(res => {
        if (res.code === 200) {
          if (res.data) {
            callback()
          } else {
            callback(new Error('用户名已存在，不允许重复添加'))
          }
        }
      })
    }
    var validateMail = (rule, value, callback) => {
      // 验证邮箱名的唯一性
      validMail({
        validMail: this.formData.email
      }).then(res => {
        if (res.code === 200) {
          if (res.data) {
            callback()
          } else {
            callback(new Error('邮箱已存在，不允许重复绑定'))
          }
        }
      })
    }
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        if (this.type === 'save') {
          callback(new Error('请输入密码'))
        } else {
          callback()
        }
      } else {
        if (this.formData.password !== '') {
          this.$refs.form.validateField('checkPassword')
        }
        callback()
      }
    }
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        if (this.type === 'save') {
          callback(new Error('请再次输入密码'))
        } else {
          callback()
        }
      } else if (value !== this.formData.password) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    return {
      roleList: [],
      formData: Object.assign({}, defaultData),
      saveRules: {// 新增规则
        username: [rules.custom(validateName), rules.required('用户名不能为空'), rules.len(1, 24)],
        password: [rules.required('请输入密码'), rules.password(validatePass), rules.len(6, 16)],
        checkPassword: [rules.required('请再次输入密码'), rules.password(validatePass2), rules.len(6, 16)],
        email: [rules.custom(validateMail), rules.email(), rules.required('邮箱必填')],
        enabled: [rules.required(null, 'blur', 'boolean')],
        roleid: [rules.required('至少勾选一个角色', 'change')]
      }
    }
  },
  methods: {
    save(state) { // 添加
      this.$refs.form.validate((valid, item) => {
        if (valid) {
          var flag = true
          for (var i = 0; i < this.formData.password.length; i++) { // 循环每个字符，检查是否存在中文
            var char = this.formData.password.charCodeAt(i)
            if (char > 0 && char < 255 && char !== 32) {
              flag = true
            } else {
              flag = false
              break
            }
          }
          if (flag) {
            this.loading = true
            this.formData.password = this.$cms.encrypt(this.formData.password)
            adminSave(this.formData).then(res => {
              if (res.code === 200) {
                this.$successMessage('新增成功')
                this.loading = false
                if (state) {
                  this.rest()
                  this.$routerLink('/admin/list')
                } else {
                  this.rest()
                }
              } else {
                this.$errorMessage(res.message)
                this.loading = false
              }
            })
          } else {
            this.$errorMessage('密码不能为中文')
          }
        }
      })
    },
    getRoleList() { // 获取角色列表
      coreRolePage({ size: 999999, page: 0 }).then(res => {
        if (res.code === 200) {
          this.roleList = res.data.content
        }
      })
    }
  },
  activated() {
    this.autoFocus()
    if (this.$route.query.type === 'save') {
      this.formData = Object.assign({}, defaultData)
    }
  },
  created() {
    this.getRoleList()
  }
}
</script>




